            </h1>
            <hr />
          </div>

          <div class="col-12" v-else>
            <h1 class="h3 text-center mt-3 mb-3 text-mainColor">
              
            </h1>
            <hr />
            <p class="h4 text-center">10</p>
          </div>
        </div>

        <div class="row m-1 m-sm-0" v-if="status.is_paid === false">
          <div class="col-12 col-lg-6 table">
