                      >{{ item.product.title }}</span
                    >
                  </td>
                  <td class="align-middle text-right">
                    {{ item.product.price | currency }}
                  </td>
                  <td class="align-middle">x{{ item.qty }}</td>
                  <td class="align-middle d-none d-sm-table-cell text-right">
