<script>
// $jquery
import $ from 'jquery';

export default {
  name: 'Checkout',
  data() {
    return {
      // 
      cart: {},
      // 
      coupon: '',
      // 
      couponFeedBack: '',
      // 
      checkoutData: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
        payment_method: '',
      },
      // ID
      orderId: '',
      // 
      status: {
        // 
        pageIsLoading: false,
        // 
        is_paid: false,
      },
    };
  },
  methods: {
    // 
    getCart() {
      const vm = this;
      // 
      vm.status.pageIsLoading = true;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      // axios
      vm.$http.get(api).then((response) => {
        console.log(response.data);
        // 
        vm.$bus.$emit('upateCartQty');
        // 
        vm.status.pageIsLoading = false;
        // 
        vm.cart = response.data.data;
      });
    },
    // 
    postCoupon() {
      const vm = this;
      // 
      vm.status.pageIsLoading = true;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`;
      // 
      const coupon = {
        code: vm.coupon,
      };
      // axios
      vm.$http.post(api, { data: coupon }).then((response) => {
        console.log(response.data);
        // 
        if (response.data.success) {
          vm.couponFeedBack = '';
        } else {
          vm.couponFeedBack = '';
        }
        // 
        vm.status.pageIsLoading = false;
        // 
        vm.getCart();
      });
    },
    submitCheckoutData() {
      const vm = this;
      // 
      vm.status.pageIsLoading = true;
      // 
      const order = vm.checkoutData;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
      // vee-validateapi validate()
      vm.$refs.checkoutForm.validate().then((result) => {
        // 
        if (result) {
          vm.$http.post(api, { data: order }).then((response) => {
            console.log('', response);
            // ID
            vm.orderId = response.data.orderId;
            // 
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`;
            vm.$http.post(url).then((res) => {
              console.log(res);
              if (res.data.success) {
                // 
                vm.status.pageIsLoading = false;
                // 
                vm.status.is_paid = true;
                // 
                vm.$bus.$emit('upateCartQty');
                // 
                $('html,body').scrollTop(0);
                // 5
                setTimeout(() => vm.$router.push({ path: '/home' }), 10000);
              }
            });
          });
        }
      });
    },
    // 
    goBack() {
      this.$router.back();
    },
  },
  created() {
    // 
    this.getCart();
    // 
    $('html,body').scrollTop(0);
  },
};
</script>

