    <div class="container mt-3 mt-sm-5 mb-5 Whole_box">
      <div class="row">
        <!--  -->
        <div class="col-md-3 sticky_container d-none d-sm-block">
          <Sidebar />
        </div>
        <!--  -->
        <div class="col-md-9">
          <div class="row">
            <div class="col-md-6 text-center">
              <img :src="product.imageUrl" alt="" class="product_img" />
            </div>
            <div class="col-md-6 card_content">
              <h1 class="text-secColor">{{ product.title }}</h1>
              <span class="card-text text-left mt-3">
                NT{{ product.price | currency }}
              </span>
              <p class="text-justify text-secColor mt-4">
                {{ product.description }}
              </p>
              <form action="#" method="post">
                <div class="d-flex justify-content-between mt-4">
                  <div class="input_group">
                    <input
                      type="button"
                      value="-"
                      class="input_button"
                      @click="addCartQty(tempQty - 1)"
                    />
                    <input
                      type="number"
                      class="text-center input_number"
                      step="1"
                      min="1"
                      max="99"
                      name="quantity"
                      value="1"
                      title=""
                      size="2"
                      v-model.number="tempQty"
                    />
                    <input
                      type="button"
                      value="+"
                      class="input_button"
                      @click="addCartQty(tempQty + 1)"
                    />
                  </div>
                  <button
                    type="submit"
                    class="btn customize_btn btn_color"
                    @click.prevent="checkCart(product.id, $event)"
                    v-if="product.is_enabled === 1"
                  >
                    
                  </button>
