              <swiper class="swiper pt-3" :options="swiperOption">
                <swiper-slide
                  v-for="(item, index) in recordProducts"
                  :key="index"
                >
                  <div class="position">
                    <a href="#!" @click.prevent="goToProductDetail(item.id)">
                      <img :src="item.imageUrl" alt="" class="swiper_img" />
                      <div class="overlay p-2"></div>
                    </a>
                  </div>
