<script>
// $jquery
import $ from 'jquery';

import Sidebar from '@/components/front/Sidebar.vue';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import 'swiper/css/swiper.css';

export default {
  name: 'Product_detail',
  components: {
    Sidebar,
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      // swiper 
      swiperOption: {
        // 
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        // 
        slidesPerView: 4,
        // slidepx
        spaceBetween: 50,
        // 
        breakpoints: {
          1024: {
            slidesPerView: 3,
            spaceBetween: 40,
          },
          769: {
            slidesPerView: 2,
            spaceBetween: 30,
          },
          375: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
        },
      },
      // 
      product: {},
      // 
      tempCart: [],
      // 
      tempQty: 1,
      // 
      recordProducts: [],
      // 
      status: {
        // 
        pageIsLoading: false,
      },
    };
  },
  methods: {
    // 
    getProductDetail() {
      const vm = this;
      // 
      vm.status.pageIsLoading = true;
      // id
      let id = '';
      id = vm.$route.params.id;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      // axios
      vm.$http.get(api).then((response) => {
        console.log(response.data);
        // 
        if (response.data.success) {
          // 
          vm.product = response.data.product;
          // saveRecord
          vm.saveRecord();
        } else {
          // 
          // 
          vm.$router.push({ path: '/products/all' });
        }
        // 
        vm.status.pageIsLoading = false;
      });
    },
    // 
    addCartQty(qty) {
      const vm = this;
      // 
      let tempQty = qty;
      // 
      // 0
      if (tempQty <= 0) {
        // 1
        tempQty = 1;
      } else if (tempQty > 99) {
        // 99
        // 99
        tempQty = 99;
      }
      // 
      vm.tempQty = tempQty;
    },
    // 
    getCart() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      // axios
      vm.$http.get(api).then((response) => {
        console.log(response.data);
        // 
        vm.tempCart = response.data.data;
      });
    },
    // 
    checkCart(id, event) {
      const vm = this;
      // 
      const i = '<i class="fas fa-spinner fa-spin"></i>';
      $(event.target).append(i);
      // 
      $(event.target).attr('disabled', true);
      // 
      const cart = {
        product_id: id,
        qty: vm.tempQty,
      };
      console.log(vm.tempQty);
      // 
      const check = vm.tempCart.carts.find((item) => item.product_id === id);
      console.log(check);
      // 
      if (check !== undefined) {
        // 
        cart.qty = check.qty + vm.tempQty;
        const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${check.id}`;
        // axios
        vm.$http.delete(url).then((response) => {
          console.log(response.data);
          vm.addtoCart(cart, event);
        });
      } else {
        vm.addtoCart(cart, event);
      }
    },
    // 
    addtoCart(cart, event) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      // axios
      vm.$http.post(api, { data: cart }).then((response) => {
        console.log(response.data);
        // 
        if (response.data.success) {
          // 
          vm.$bus.$emit('upateCartQty');
          // 
          vm.getCart();
        }
        // 
        $(event.target)
          .children()
          .remove();
        // 
        $(event.target).attr('disabled', false);
      });
    },
    // 
    saveRecord() {
      const vm = this;
      // 
      const check = vm.recordProducts.find((item) => item.id === vm.product.id);
      // 
      if (check === undefined) {
        // 
        vm.recordProducts.push(vm.product);
      }
      //  localStorage
      localStorage.setItem('save', JSON.stringify(vm.recordProducts));
    },
    // 
    getRecord() {
      //  localStorage
      const data = JSON.parse(localStorage.getItem('save'));
      // 
      if (data.length !== null) {
        // 
        this.recordProducts = data;
      }
    },
    // 
    goToProductDetail(productId) {
      this.$router.push({ path: `/product_detail/${productId}` });
    },
  },
  watch: {
    // 
    tempQty(value) {
      const vm = this;
      // 
      let tempQty = value;
      // 
      // 0
      if (tempQty <= 0) {
        // 1
        tempQty = 1;
      } else if (tempQty > 99) {
        // 99
        // 99
        tempQty = 99;
      }
      // 
      vm.tempQty = tempQty;
    },
    // 
    $route() {
      const vm = this;
      // 
      vm.getProductDetail();
      // 
      $('html,body').scrollTop(0);
    },
  },
  created() {
    // 
    this.getProductDetail();
    // 
    this.getCart();
    // 
    this.getRecord();
    // 
    $('html,body').scrollTop(0);
  },
};
</script>

